/* btn */
.btn {
    white-space: nowrap;
}
.btn--sm {
    font-size: r(13);
    padding: 4px 14px;
    border-radius: 34px;
    @include xl {
        font-size: r(18);
        padding: 8px 19px;
    }
}
.btn--lg {
    font-size: r(18);
    padding: 11px 29px;
    border-radius: 54px;
    @include xl {
        font-size: r(23);
        padding: 14px 34px;
    }
}
.btn--primary {
    background: $color-blue;
    border-color: $color-blue;
    &:hover {
        background: $color-l-blue;
        border-color: $color-l-blue;
    }
}
.btn--secondary {
    background: $color-root;
    border-color: $color-root;
    &:hover {
        background: $color-l-root;
        border-color: $color-l-root;
    }
}
.btn--more {
    padding-right: 84px;
    padding-left: 84px;
}
