/* s-team-brand */
.s-team-brand {
    padding-top: 67px;
    padding-bottom: 27px;
    @include xl {
        padding-top: 135px;
        padding-bottom: 142px;
    }
    color: $color-white;
    & > .container {
        padding-right: 0;
        padding-left: 0;
    }
    .p-section__title {
        max-width: 675px;
        margin-top: 10px;
        margin-bottom: 40px;
        span {
            color: $color-2;
        }
    }
}
.s-team-brand__title {
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.625;
    margin-bottom: 20px;
    opacity: .6;
    @include md {
        margin-top: 40px;
    }
    @include xl {
        margin-top: 0;
    }
}
.brand-list {
    max-width: 675px;
    opacity: .6;
    @include xl {
        // margin-left: -27px;
    }
    img {
        margin-bottom: 40px;
    }
}
