/* b-timline */
.b-timline__item {
    position: relative;
    padding-top: 20px;
    padding-bottom: 18px;
    padding-left: 40px;
    @include xl {
        padding-left: 64px;
    }
    @include fc {
        padding-top: 0;
        &::after {
            content: "";
            position: absolute;
            top: 20px;
            left: -14px;
            width: 30px;
            height: 30px;
            background: $color-white;
            border: 2px solid $color-root;
            border-radius: 50%;
            @include xl {
                top: 24px;
            }
        }
    }
    @include lc {
        padding-bottom: 0;
    }
    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        margin: auto;
        width: 3px;
        background: $color-root;
    }
}
.b-timline__text {
    position: relative;
    letter-spacing: -.02em;
    margin-top: -2px;
    &::before {
        content: "";
        position: absolute;
        top: 12px;
        left: -56px;
        width: 36px;
        height: 21px;
        background: $color-root;
        background: #0C1447;
        border-radius: 50%;
        @include xl {
            left: -80px;
        }
    }
}
