/* b-card */
.b-card {
    .b-card__wrap {
            margin-bottom: 18px;
            @include xl {
                margin-bottom: 34px;
            }
        }
}
.b-card__item {
    position: relative;
    overflow: hidden;
    background: $color-gray;
    border-radius: $b-radius-20;
    padding: 28px 18px;
    max-width: 380px;
    margin-right: auto;
    margin-left: auto;
    @include lg {
        max-width: 100%;
    }
    @include xl {
        padding-right: 28px;
        padding-left: 28px;
    }
    // margin-bottom: 34px;
    &:hover {
        .b-card__title {
            color: $color-blue;
        }
    }
}
.b-card__left {
    overflow: hidden;
    max-width: 380px;
    background: $color-white;
    border-radius: $b-radius-14;
}
.b-card__right {
    padding-top: 18px;
    @include xl {
        padding-top: 0;
        padding-left: 28px;
    }
    @include lg {
        padding-left: 58px;
    }
}
.b-card__title {
    margin-bottom: 10px;
    @include xl {
        margin-bottom: 30px;
    }
}
.b-card__date {
    color: $color-05-root;
}
.b-list--card {
    @include xl {
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    }
    li {
        @include xl {
            -webkit-flex: 0 1 50%;
            -ms-flex: 0 1 50%;
            flex: 0 1 50%;
            width: 50%;
            max-width: 50%;
            min-width: 50%;
        }
    }
}
.b-list--card {
    margin-bottom: 0;
}
