/* s-team-list */
.s-team-list {
   padding-bottom: 43px;
}
.b-team__item {
    &:not(:last-child) {
        margin-bottom: 54px;
        @include md {
            margin-bottom: 80px;
        }
   }
}
.b-team__content {
    @include md {
        padding-top: 3px;
        padding-right: 5px;
        padding-left: 7px;
    }
}
.b-team__name {
    margin-bottom: 0;
    @include md {
        margin-bottom: 7px;
    }
}
.b-team__position {
    margin-bottom: 7px;
    color: $color-06-root;
    @include md {
        margin-bottom: 20px;
    }
}
.b-team__text {
    margin-bottom: 30px;
}
.b-team__list {
    margin-bottom: 0;
    @include xl {
        column-gap: 54px;
        column-width: 294px;
    }
    li {
        @include xl {
            vertical-align: top;
            display: inline-block;
        }
        &:not(:last-child) {
            margin-bottom: 13px;
            @include md {
                margin-bottom: 23px;
            }
            @include xl {
                margin-bottom: 28px;
            }
       }
    }
}
.s-team-list__img {
    position: absolute;
    right: -7px;
    bottom: -86px;
    z-index: 1;
    height: 169px;
    @include xl {
        right: 0;
        width: auto;
    }
}
.b-team__img {
    position: relative;
    margin-bottom: 20px;
    @include md {
        margin-left: 2px;
    }
    img {
        overflow: hidden;
        max-width: 309px;
        width: 309px;
        border-radius: 20px;
    }
}
.b-team__bar {
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 19px 20px 12px;
    border-top-right-radius: 10px;
    background: $color-white;
}
.b-team__icon {
    display: block;
    background-color: transparent;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    &:not(:last-child) {
        margin-right: 15px;
    }
}
