/* s-hero--team */
.s-hero--team {
    margin-bottom: 90px;
    padding-top: 105px;
    @include xl {
        padding-top: 184px;
        padding-bottom: 104px;
    }
    @include max-md {
        &::before {
            display: none;
        }
    }
    @include md {
        padding-bottom: 60px;
        margin-bottom: 100px;
    }
    @include xl {
        height: 802px;
        margin-bottom: 410px;
        padding-bottom: 0;
    }
    .s-hero__top {
        max-width: 1080px;
        margin-right: auto;
        margin-left: auto;
        margin-bottom: 50px;
        @include md {
            margin-bottom: 20px;
        }
        @include xl {
            margin-bottom: 57px;
        }
    }
    .s-hero__title {
        margin-bottom: 0;
    }
    .s-hero__bottom {
        position: relative;
        top: 20px;
        left: 0;
        right: 0;
        padding-bottom: 0;
        margin-left: -37px;
        margin-right: -37px;
        @include md {
            top: 0;
            left: auto;
            right: auto;
            margin-right: auto;
            margin-left: auto;

        }
        @include xl {
            top: -47px;
        }
    }
    .s-hero__img {
       @include max-md {
        object-fit: cover;
        object-position: top;
        // width: 100%;
        height: 100%;
        width: 100%;
       }


        margin-top: 0;
        margin-left: 0;
    }
}
