/* b-tag */
.b-tag {
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-top: -15px;
    margin-left: -11px;
}
.b-tag__item {
    font-size: r(18);
    color: $color-root;
    line-height: 1.056;
    text-align: center;
    letter-spacing: -.01em;
    padding: 7px 14px;
    background: $color-1;
    margin-left: 11px;
    margin-top: 15px;
}
