/* s-home-team */
.s-home-team {
     padding-top: 200px;
     padding-bottom: 97px;
    @include md {
        padding-top: 132px;
        padding-bottom: 131px;
    }
}
@media (max-height: 414px) {
    .s-home-team {
        padding-top: 0;
    }
}
.s-home-team__title {
    max-width: 1080px;
    padding-left: 0;
    margin-bottom: 64px;
}
.s-home-team-list {
    margin-bottom: 60px;
    @include xl {
        margin-bottom: 105px;
    }
}
.s-home-team-list__inner {
    width: 100%;
    padding-bottom: 10px;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
}
.s-home-team-list__card {
    max-width: 239px;
    min-width: 239px;


    margin-right: 21px;
    @include lg {
        max-width: calc(25% - 16px);
        min-width: calc(25% - 16px);
        margin-bottom: 30px;
        min-width: auto;
    }
    &:last-child {
        margin-right: 0;
    }
}
.s-home-team-list__img {
    overflow: hidden;
    border-radius: 30px;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 30px;
    @include lg {
        margin-bottom: 42px;
    }
}
.s-home-team-list__name {
    line-height: 1;
    font-weight: 500;
    letter-spacing: -.02em;
    margin-bottom: 15px;
}
.s-home-team-list__post {
    line-height: 1.588235294117647;
}
