/* templ */
.t-body {
    font-family: $font-family-base;
    font-size: $font-size-root;
    color: $color-root;
    line-height: $line-height-root;
    overflow-x: hidden;

}
.t-wrapper {
    padding-right: 7px;
    padding-left: 7px;
    @include xl {
        padding-right: 0;
        padding-left: 0;
    }
}
.p-section {
    position: relative;
}
.p-section__title {
    letter-spacing: -.02em;
}
.s-hero__title {
    font-weight: 500;
    letter-spacing: -0.02em;
}
.container {
    max-width: $container-main;
}
.container--prl-0 {
    @include max-xl {
        padding-right: 0;
        padding-left: 0;
    }
}
.container-narrow,
.container-narrow-sm,
.container-narrow-xs {
    margin-right: auto;
    margin-left: auto;
}
.container-narrow {
    max-width: $container-narrow;
}
.container-narrow-sm {
    max-width: $container-narrow-sm;
}
.container-narrow-xs {
    max-width: $container-narrow-xs;
}
.bg-1,
.bg-2,
.bg-3 {
    position: relative;
    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 18px;
        background-image: url("data:image/svg+xml,%3Csvg width='29' height='18' viewBox='0 0 29 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14.5205 17.9976L0.237433 -0.000628642L28.8035 -0.000631139L14.5205 17.9976Z' fill='white'/%3E%3C/svg%3E%0A");
        background-color: transparent;
        background-repeat: repeat-x;
        background-size: 29px 18px;
    }
}
.bg-1 {
    background-color: $color-1;
    }
.bg-2 {
    background-color: $color-2;
}
.bg-3 {
    background-color: $color-gray;
}
.bg-4 {
    background-color: $color-root;
}
.bg-d-bottom {
    &::before {
        top: auto;
        bottom: 0;
        -webkit-transform: scale(1) rotate(180deg);
        transform: scale(1) rotate(180deg);
    }
}
.bg-inner {
    @include max-xl {
        width: calc(100% + 44px);
        padding-right: 22px;
        padding-left: 22px;
        position: relative;
        left: -22px;
    }
}
.bg-outer {
    @include max-xl {
        width: calc(100% + 14px);
        padding-right: 22px;
        padding-left: 22px;
        position: relative;
        left: -7px;
    }
}
.bg-no-decor {
    &::before {
        display: none;
    }
}
