/* b-review */
.b-review__img {
    overflow: hidden;
    max-width: 65px;
    width: 65px;
    height: 65px;
    margin-right: 19px;
    border-radius: 50%;
}
.row--review {
    margin-left: -30px;
    margin-right: -30px;
    & [class^="col-"] {
        padding-right: 30px;
        padding-left: 30px;
    }
}
.b-review__name {
    font-weight: 500;
    margin-bottom: 6px;
}
.b-review__info {
    margin-bottom: 30px;
}
.b-review__post {
    margin-bottom: 3px;
}
.b-review__item {
    margin-top: 40px;
    margin-right: auto;
    margin-left: auto;
    @include xl {
        margin-top: 0;
    }
}
.b-review__content {
    position: relative;
    padding: 30px;
    @include xl {
        padding: 36px;
    }
    background: $color-white;
    border-radius: 20px;
    &::after {
        content: "";
        position: absolute;
        top: -12px;
        left: 13px;
        width: 31px;
    height: 15px;
    background-image: url("data:image/svg+xml,%3Csvg width='31' height='15' viewBox='0 0 31 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15.5 0L30.6554 15H0.344556L15.5 0Z' fill='white'/%3E%3C/svg%3E%0A");
        background-color: transparent;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }
}
