/* s-post-stat */
.s-post-stat {
    padding-bottom: 10px;
    margin-bottom: 20px;
    @include max-xl {
        background-color: $color-white;
    }
    .b-stat {
        position: relative;
        @include xl {
            top: -193px;
        }
        &::before {
            @include max-xl {
                border-right-color: $color-white;
            }
            @include xl {
            border-left-color: $color-1;
        }

        }
    }
}
