/* s-hero--work */
.s-hero--work {
    padding-top: 137px;
    padding-bottom: 40px;
    @include xl {
        padding-top: 237px;
        padding-bottom: 0;
    }
    .s-hero__top {
        margin-bottom: 28px;
        @include xl {
            margin-right: 60px;
            margin-bottom: 67px;
        }
    }
    .s-hero__bottom {
        padding-bottom: 0;
    }
    .s-hero-bottom__text {
        margin-bottom: 12px;
        @include max-xl {
            font-size: r(15);
        }
    }
    .s-hero-bottom__img {
        &:not(:last-child) {
            margin-right: 20px;
        }
        @include lg {
            max-width: 100%;
            width: auto;
            height: auto;
        }
    }
    .s-hero-bottom__brand {
        @include max-lg {
            display: block;
            width: 100%;
            overflow-x: auto;
            -webkit-overflow-scrolling: touch;
        }
    }
    .s-hero__img {
        margin-bottom: 30px;
        @include md {
            max-width: 100%;
            width: auto;
            height: auto;
        }
        @include lg {
            margin-bottom: 0;
        }
        @include xl {
            margin-top: -47px;
            margin-left: 74px;
        }
    }
}
