/* t-header */
.t-header {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    // margin-left: -7px;
    // margin-right: -7px;
    z-index: 1070;
    background-color: $color-white;
    -webkit-transition: .25s ease-in-out;
    transition: .25s ease-in-out;
    border-bottom: 2px solid transparent;
    @include xl {
        padding-top: 30px;

    }
    .t-header__logo {
        display: block;
        width: 80px;
        height: 32px;
        @include md {
            width: auto;
            height: auto;
        }
    }
    &.t-header--fixed {
        border-bottom-color: $color-gray;
        padding-top: 0;
    }
    .navbar {
        padding: 26px 22px;
        background-color: $color-white !important;
        @include xl {
            padding-left: 0;
            padding-right: 0;
        }
    }
    .navbar-collapse {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1080;
        background-color: $color-root;
        animation: none;
        overflow-y: auto;
        -webkit-overflow-scrolling: touch;
        @include md {
            position: relative;
            background-color: $color-white;
        }
    }
    .navbar-nav {
        margin-top: 120px;
        @include md {
            margin-top: 0;
        }
        @include xl {
            margin-left: 88px;
        }
    }
    .nav-item {
        margin-bottom: 44px;
        @include md {
            margin-bottom: 0;
        }
    }
    .collapsing {
        -webkit-transition: none;
        transition: none;
        display: none;
    }
    .navbar-expand-md .navbar-nav .nav-link {
        font-size: r(24);
        padding: 0 r(24);
        color: $color-white;
        @include md {
            font-size: r(18);
            color: $color-root;
        }
        &:hover {
            color: $color-blue;
        }
    }
    .navbar-toggler {
        position: relative;
        z-index: 1090;
        padding: 0;
        border-color: transparent;
        .navbar-toggler-icon {
            background-position: center;
            background-size: 22px 20px;
            background-image: url("data:image/svg+xml,%3Csvg width='22' height='20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2 10.146h18M2 2h18M2 18.291h18' stroke='%230C1447' stroke-width='2.2' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
        }
        &:not(.collapsed) {
            position: fixed;
            top: 30px;
            right: 24px;
            .navbar-toggler-icon {
                background-image: url("data:image/svg+xml,%3Csvg width='22' height='22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2 2l18.103 18.103M20.103 2L2 20.103' stroke='%23fff' stroke-width='2.2' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
            }
        }
    }
}
@media (min-width: 768px) and (max-width: 1199px) {
    .container--header {
        padding-right: 0;
        padding-left: 0;
    }
}
.btn--m-menu {
    position: fixed;
    right: 0;
    left: 0;
    margin: auto;
    width: 222px;
    bottom: 30px;
    @include max-lg {
        font-size: r(18);
        padding: 11px 29px;
        border-radius: 54px;
    }
    @include md {
        position: static;
        margin: 0;
        width: auto;
    }
}
@media (max-height: 414px) and (max-width: 991px) {
    .btn--m-menu {
        right: 0;
        left: auto;
        margin-right: 28px;
    }
}
.t-header__btn {
    margin-right: 20px;
    @include md {
        margin-right: 0;
    }
}
.t-body--header-transparent {
    .bg-light,
    .t-header,
    .t-header .navbar {
        background-color: transparent !important;
    }
    .t-header .navbar-collapse {
        @include md {
            background-color: transparent !important;
        }
    }
    .t-header--fixed {
        &.t-header,
        .t-header .navbar {
            background-color: $color-white !important;
        }
    }
}
