/* s-work-direction */
.s-work-direction {
    padding-top: 68px;
    padding-bottom: 40px;
    @include xl {
        padding-top: 83px;
        padding-bottom: 100px;
    }
    .container-narrow {
        max-width: 1277px;
    }
    .p-section__title {
        margin-bottom: 58px;
        @include xl {
            margin-bottom: 90px;
        }
    }
    .s-work-direction__item {
        margin-bottom: 20px;
        @include lg {
            margin-left: 14px;
        }
        @include xl {
            margin-bottom: 53px;
        }
    }
    [class*="col-"]:first-child .s-work-direction__item,
    [class*="col-"]:nth-child(4n+4) .s-work-direction__item {
        margin-left: 0;
    }
}
.s-work-direction__text {
    margin-top: -14px;
    margin-bottom: 0;
    margin-left: 34px;
}
