/* b-case */
.row--case {
    @include xl {
        margin-left: -11px;
        margin-right: -11px;
    }
    & [class^="col-"] {
        padding-right: 15px;
        padding-left: 15px;
        @include xl {
        padding-right: 11px;
        padding-left: 11px;
        }
    }
}
.b-case__item {
    position: relative;
    margin-bottom: 28px;
    @include xl {
        margin-bottom: 0;
    }
}
.b-case__img {
    overflow: hidden;
    height: 275px;
    background-color: $color-gray;
    margin-bottom: 18px;
    padding: 15px;
    border-radius: 20px;
    @include xl {
        margin-bottom: 21px;
    }
}
.b-case__text {
    margin-bottom: 0;
    margin-left: -1px;
}
.b-case__item:hover {
    .b-case__text {
        color: $color-blue;
    }
}
