/* s-home-plus */
.s-home-plus {
    padding-top: 51px;
    padding-bottom: 70px;
    @include xl {
        padding-top: 132px;
        padding-bottom: 150px;
    }
}
.s-home-plus__title {
    margin-bottom: 58px;
}
.s-home-plus-card {
    margin-bottom: 30px;
    @include xl {
        margin-bottom: 90px;
        margin-left: 42px;
    }
}
.s-home-plus-card__title {
    font-weight: 500;
    margin-bottom: 8px;
}
.s-home-plus-card__img {
    position: relative;
    min-width: 100px;
    max-width: 100px;
    padding-right: 9px;
    text-align: center;
    @include xl {
        left: -13px;
        margin-top: 21px;
        min-width: 176px;
        max-width: 176px;
    }
    img {
        @include xl {
            //min-width: 167px;
            //min-height: 148px;
        }
    }
}
.row--plus {
    & > [class^="col-"] {
        &:last-child {
            @include xl {
                position: relative;
                top: 111px;
            }
        }
    }
}
.s-home-plus-card__text {
    max-width: 420px;
}
