/* t-footer */
.t-footer {
    padding: 59px 0 42px;
    @include xl {
        padding-top: 103px;
        padding-bottom: 35px;
    }
}
.t-footer__img {
    margin-right: 40px;
    margin-bottom: 10px;
    @include xl {
        margin-top: -12px;
        margin-right: 131px;
        margin-bottom: 0;
        margin-left: 23px;
    }
}
.btn--footer {
    position: relative;
    top: -14px;
    @include md {
        top: 0;
    }
}
@media (max-height: 414px) {
    .btn--footer {
        top: 0;
    }
}
.t-footer__menu {
    margin-bottom: 52px;
    @include xl {
        max-width: 845px;
        margin-bottom: 112px;
    }
}
.footer-menu__sub-menu {
    margin-top: 40px;
    @include xl {
        margin-top: 0;
    }
}
.footer-menu__sub-menu--2 {
    @include xl {
        margin-left: -30px;
    }
}
.footer-menu__sub-menu--3 {
    @include xl {
        margin-left: -59px;
    }
}
.footer-menu__title {
    font-weight: 500;
    letter-spacing: -.02em;
    margin-bottom: 21px;
}
.footer-menu__item {
    margin-bottom: 13px;
}
.footer-menu__link {
    color: $color-root;
}
.btn--menu-footer {
    position: relative;
    top: 16px;
}

/* privacy-policy */
.privacy-policy {
    font-size: .875rem;
    line-height: 1.4285714285714286;
    color: $color-root;
    opacity: .5;
}
.privacy-policy__link {
    display: inline-block;
    color: inherit;
    margin-right: 18px;
    margin-bottom: 26px;
    @include xl {
        margin-bottom: 0;
    }
}
.privacy-policy__text {
    margin-bottom: 0;
    @include xl {
        margin-right: 156px;
    }
}
