/* s-form */
.t-body--call-form .navbar-toggler,
.t-body--call-form .t-header__btn,
.t-body--call-form .navbar-nav,
.t-body--call-form .btn--m-menu {
    display: none;
}
.s-form {
    .p-section__title {
        margin-bottom: 9px;
    }
}
.s-form-info,
.b-form--call {
    @include max-xl {
        margin-right: -7px;
        margin-left: -7px;
        padding-top: 67px;
        padding-right: 22px;
        padding-left: 22px;
    }
    @include xl {
        padding-top: 105px;
        padding-top: 167px;
    }
    .b-form__title {
        margin-bottom: 9px;
    }
    .b-form__text {
        margin-bottom: 30px;
    }
}
.s-form-info {
    position: relative;
    padding-top: 110px;
    padding-bottom: 64px;
    background: $color-1;
    @include xl {
        padding-top: 166px;
        padding-bottom: 193px;
    }
    &::before,
    &::after {
        content: "";
        position: absolute;
        background-color: transparent;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }
    &::before {
        bottom: -34px;
        right: 0;
        left: 0;
        width: 79px;
        height: 69px;
        margin: auto;
        @include xl {
            top: 0;
            bottom: 0;
            right: -28px;
            left: auto;
        }
        background-image: url('../img/form-call/0.svg');
    }
    &::after {
        @include max-xl {
            display: none !important;
        }
        left: 0;
        bottom: -225px;
        width: 137px;
        height: 278px;
        background-image: url('../img/form-call/1.svg');
    }
}
.s-form-info__inner {
    max-width: 472px;
    margin-right: auto;
    margin-left: auto;
}
.s-form-info__text {
    margin-bottom: 20px;
    @include xl {
        margin-bottom: 28px;
    }
}
.s-form-card {
    padding: 32px 26px;
    background: $color-white;
    border: 3px solid $color-root;
    @include md {
       padding: 36px 46px 46px;
    }
}
.s-form-card__img {
    overflow: hidden;
    max-width: 80px;
    width: 80px;
    height: 80px;
    margin-bottom: 14px;
    border-radius: $b-radius-round;
}
.s-form-card__name {
    font-size: 27px;
    line-height: 145%;
    margin-bottom: 26px;
}
.s-form-card__list {
    margin-bottom: 20px;
    li {
        &:not(:last-child) {
            margin-bottom: 22px;
        }
    }
    svg {
        margin-right: 24px;
    }
}
.s-form-card__text {
    margin-bottom: 22px;
    color: $color-05-root;
}
.b-form--call {
    position: relative;
    height: 100%;
    border-bottom: 2px solid $color-gray;
    margin-bottom: 100px;
    @include xl {
        margin-bottom: 0;
    }
    &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: -90px;
        width: 81px;
        height: 166px;
        background-image: url('../img/form-call/1.svg');
        background-color: transparent;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        @include xl {
            display: none !important;
        }
    }
    .b-form__inner {
        margin-right: auto;
        margin-left: auto;
        max-width: 528px;
    }
    textarea {
        height: 97px !important;
    }
    .b-form__submit {
        margin-top: 13px;
        margin-bottom: 9px;
        padding: 9px 70px;
    }
    .b-form__confirm {
        padding-top: 47px;
        img {
            margin-bottom: 27px;
        }
        p {
            max-width: 340px;
        }
    }
}
.s-form-quote {
    margin-right: auto;
    margin-left: auto;
    padding-top: 136px;
    padding-bottom: 90px;
    max-width: 770px;
    @include xl {
        padding-top: 60px;
    }
}
.s-form-quote__text {
    margin-bottom: 30px;
}
.s-form-quote__img {
    overflow: hidden;
    max-width: 56px;
    width: 56px;
    height: 56px;
    margin-right: 18px;
    border-radius: $b-radius-round;
}
.s-form-quote__name {
    letter-spacing: -.01em;
}
.s-form-quote__post {
    color: $color-07-root;
}
