/* s-process-access */
.s-process-access {
    padding: 85px 0 70px;
    @include xl {
        padding-top: 122px;
        padding-bottom: 116px;
    }
}
.process-access-map {
    padding-right: 7px;
    padding-left: 7px;
    max-width: 350px;
    @include sm {
        margin-right: auto;
        margin-left: auto;
    }
    @include xl {
        max-width: 100%;
        margin-right: 0;
        margin-left: 0;
        padding-right: 0;
        padding-left: 0;
    }
}
.process-access-map__item {
    position: relative;
    height: 210px;
    @include xl {
        height: 100%;
        min-height: 100%;
        margin-bottom: 24px;
    }
    &::before,
    &::after {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        background-color: transparent;
        background-repeat: no-repeat;
    }
    &::after {
        top: 120px;
        left: 17px;
        width: 21px;
        height: 23px;
        background-image: url("data:image/svg+xml,%3Csvg width='21' height='23' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2.392 17.285c0 2.309 2.5 3.752 4.5 2.598l10.5-6.063c2-1.154 2-4.041 0-5.196l-10.5-6.062c-2-1.155-4.5.289-4.5 2.598v12.125z' fill='%230C1447' stroke='%23EAEDFF' stroke-width='4'/%3E%3C/svg%3E");
        -webkit-transform: scale(1) rotate(90deg);
        transform: scale(1) rotate(90deg);
        @include xl {
            top: 19px;
            left: 0;
            right: 0;
            margin-left: auto;
            margin-right: auto;
            -webkit-transform: scale(1) rotate(0deg);
        transform: scale(1) rotate(0deg);
        }
    }
}
.process-access-map__item--1,
.process-access-map__item--4 {
    &::before {
        @include xl {
            top: 28px;
            height: 4px;
            background-image: url("data:image/svg+xml,%3Csvg width='1082' height='4' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M.861 1.845H1081.52' stroke='%230C1447' stroke-width='2.5' stroke-dasharray='3 3'/%3E%3C/svg%3E");
        }
    }
}
.process-access-map__item--1 {
    &::before {
        @include max-xl {
            left: 26px;
            width: 3px;
            height: 890px;
            background-image: url("data:image/svg+xml,%3Csvg width='3' height='264' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='%230C1447' stroke-width='2.5' stroke-dasharray='3 3' d='M1.271 263.095V.799'/%3E%3C/svg%3E");
            background-repeat: repeat-y;
        }
        @include xl {
            height: 3px;
            width: 1091px;
            background-repeat: repeat-x;
        }
    }
}
.process-access-map__item--4 {
    &::before {
        @include xl {
            width: 950px;
        }
    }
}
.process-access-map__item--2 {
    @include xl {
        left: 16px;
    }
}
.process-access-map__item--4 {
    @include xl {
        left: -39px;
    }
}
.process-access-map__item--5 {
    @include xl {
        left: 57px;
    }
}
.process-access-map__item--1 {
    &::after {
        @include xl {
            left: 82px;
        }
    }
}
.process-access-map__item--2 {
    &::after {
        @include xl {
            left: 56px;
        }
    }
}
.process-access-map__item--3 {
    &::after {
        @include xl {
            left: 109px;
        }
    }
}
.process-access-map__item--4 {
    &::after {
        @include xl {
            left: 173px;
            top: 18px;
        }
    }
}
.process-access-map__item--5 {
    &::after {
        @include xl {
            left: 159px;
            top: 18px;
        }
    }
}
.process-access-map__item--4,
.process-access-map__item--5 {
    &::after {
        @include xl {
            -webkit-transform: scale(1) rotate(180deg);
            transform: scale(1) rotate(180deg);
        }
    }
}
.process-access-map__item--5 {
    &::after,
    &::before {
        @include max-xl {
            display: none;
        }
    }

}
.process-arrow-1,
.process-dot-line-1,
.process-dot-line-3 {
    position: absolute;
    background-color: transparent;
    background-repeat: no-repeat;
}
.process-dot-line-1 {
    top: 100px;
    width: 23px;
    height: 373px;
    right: 0;
