/* type */
.mega-lead {
    font-size: $m-mega-lead;
    line-height: 1;
    @include xl {
        font-size: $d-mega-lead;
    }
}
.special-lead-text {
    font-size: $m-special-lead-text;
    line-height: 1;
    @include xl {
        font-size: $d-special-lead-text;
    }
}
.heading-1 {
    font-size: $m-heading-1;
    line-height: 120%;
    @include xl {
        font-size: $d-heading-1;
        line-height: 100%;
    }
}
.heading-2 {
    font-size: $m-heading-2;
    line-height: 120%;
    @include xl {
        font-size: $d-heading-2;
        line-height: 120%;
    }
}
.heading-3 {
    font-size: $m-heading-3;
    line-height: $m-line-height-3;
    @include xl {
        font-size: $d-heading-3;
        line-height: 120%;
    }
}
.heading-4 {
    font-size: $m-heading-4;
    line-height: $m-line-height-3;
    @include xl {
        font-size: $d-heading-4;
        line-height: $d-line-height-3;
    }
}
.paragraph-large {
    font-size: $m-paragraph-large;
    line-height: $m-line-height-5;
    @include xl {
        font-size: $d-paragraph-large;
        line-height:145% ;
    }
}
.paragraph-normal {
    font-size: $m-paragraph-normal;
    line-height: $m-line-height-6;
    @include xl {
        font-size: $d-paragraph-normal;
        line-height: 145%;
    }
}
.paragraph-small {
    font-size: $m-paragraph-small;
    line-height: $m-line-height-7;
    @include xl {
        font-size: $d-paragraph-small;
        line-height: 145%;
    }
}
.paragraph-extra-small {
    font-size: $m-paragraph-extra-small;
    line-height: $m-line-height-8;
    @include xl {
        font-size: $d-paragraph-extra-small;
        line-height: $d-line-height-6;
    }
}
.tiny {
    font-size: $d-tiny;
    line-height: $d-line-height-7;
}

.font-weight-500 {
    font-weight: $font-weight-500;
}
.font-weight-700 {
    font-weight: $font-weight-700;
}
.letter-spacing-02 {
    letter-spacing: $letter-spacing-02;
}

/* link */
a:hover {
    color: $color-blue;
    text-decoration: none;
    -webkit-transition: all .15s linear;
    transition: all .15s linear;
}
