.b-stat::before,
.b-stat::after {
    content: "";
    position: absolute;
    z-index: 1;
    width: 0;
    height: 0;
    border: 0 solid transparent;
    border-top-width: 0;
    right: -2px;
    bottom: -2px;
    @include xl {
        left: -3px;
        bottom: -3px;
    }
}
.b-stat::after {
    @include max-xl {
        border-bottom-width: 63px;
        border-top-width: 0;
        border-left: 72px solid $color-root;
    }
    @include xl {
        border-top-width: 0;
        border-bottom-width: 123px;
        border-right: 123px solid $color-root;
    }
}
.b-stat::before {
    @include max-xl {
        border-top-width: 63px;
        border-bottom-width: 0;
        border-right: 72px solid $color-white;
    }
    @include xl {
        border-bottom-width: 0;
        border-top-width: 123px;
        border-left: 123px solid $color-white;
    }
}
.b-stat__count {
    letter-spacing: -.06em;
    margin-bottom: 14px;
    font-weight: 500;
    @include xl {
        margin-top: -9px;
        margin-bottom: 25px;
    }
}
.b-stat__right .btn {
    position: relative;
    margin-top: 27px;
    @include xl {
        left: 6px;
        top: -13px;
    }
    .svg {
        margin-left: 8px;
    }
}
/* b-stat--type-1 */
.b-stat--type-1 {
    padding-top: 50px;
    @include xl {
        padding-top: 66px;
        border-top-style: solid;
        border-top-color: $color-root;
    }
}
