/* s-blog-list */
.s-blog-list {
    padding-top: 105px;
    padding-bottom: 40px;
    @include xl {
        padding-top: 181px;
        padding-bottom: 120px;
        border-bottom: 3px solid $color-gray;
    }
    .p-section__title {
        margin-bottom: 27px;
        @include xl {
            margin-bottom: 60px;
        }
    }
    .b-card {
        margin-bottom: 30px;
    }
    .b-card__title {
        max-width: 512px;
    }
    .b-card__left {
        max-width: 392px;
    }
    .b-card__right {
        @include lg {
            padding-left: 29px;
        }
    }
}
.s-blog-list__img-1,
.s-blog-list__img-2 {
    position: absolute;
    z-index: 1;
}
.s-blog-list__img-1 {
    top: -16px;
    right: -20px;
    width: 120px;
    @include xl {
        top: 11px;
        right: -58px;
        width: auto;
    }
}
.s-blog-list__img-2 {
    top: 15px;
    right: 44px;
}
