/* s-home-hero */
.s-hero--home {
    padding-top: 178px;
    .s-hero__top {
        margin-bottom: 160px;
        @include xl {
            margin-bottom: 285px;
        }
    }
    .s-hero__big-img {
        img {
            position: relative;
            z-index: 1;
            margin-top: -90px;
            margin-bottom: 30px;
            @include xl {
                margin-top: -194px;
                margin-bottom: 83px;
            }
        }
    }
}
.s-hero-bottom {
    position: relative;
    padding-bottom: 45px;
}
.s-hero__title {
    margin-bottom: 29px;
    text-align: center;
    @include xl {
        text-align: left;
    }
}
.s-hero-bottom__text {
    margin-bottom: 25px;
}
.s-hero__text {
    max-width: 742px;
    margin-right: auto;
    margin-left: auto;
}
.s-hero-bottom__text {
    opacity: .5;
}

.s-hero--home {
    .s-hero__title {
        & + .s-hero__text {
            margin-bottom: 46px;
        }
    }
    .e-bamp {
        position: relative;
        &::after,
        &::before {
            content: "";
            position: absolute;
            background-color: transparent;
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            @include max-xl {
                display: none;
            }
        }
        &::after {
            top: 33px;
            right: -8px;
            width: 19px;
            height: 18px;
            background-image: url("data:image/svg+xml,%3Csvg width='19' height='18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17.302 17.031S13.59 3.504.967 1.64l16.335 15.392z' fill='%23fff'/%3E%3Cpath d='M17.302 17.031S13.59 3.504.967 1.64' stroke='%230C1447' stroke-width='3' stroke-miterlimit='10' stroke-linejoin='round'/%3E%3C/svg%3E");
        }
        &::before {
            top: 40px;
            right: -5px;
            width: 28px;
            height: 23px;
            background-image: url("data:image/svg+xml,%3Csvg width='28' height='23' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M26.245 22.143S20.858.794.295 1.87l25.95 20.272z' fill='%23fff'/%3E%3Cpath d='M26.245 22.143S20.858.794.295 1.87' stroke='%230C1447' stroke-width='3' stroke-miterlimit='10' stroke-linejoin='round'/%3E%3C/svg%3E");
        }
    }
}
.s-hero-bottom__brand {
    max-width: 755px;
    margin-bottom: -5px;
    img {
        margin-bottom: 20px;
    }
}
