/* s-hero--process */
.s-hero--process {
    padding: 123px 0 50px;
    @include xl {
        padding-top: 191px;
        padding-bottom: 109px;
    }
    .s-hero__content {
        max-width: 530px;
        @include lg {
            margin-right: 76px;
        }
    }
    .s-hero__title {
        margin-bottom: 20px;
        @include xl {
            margin-bottom: 40px;
        }
    }
    .s-hero__text {
        margin-bottom: 40px;
        @include md {
            padding-right: 30px;
        }
        @include lg {
            padding-right: 0;
        }
    }
    .s-hero__img {
        max-width: 316px;
        @include lg {
            position: relative;
            top: -9px;
            left: 8px;
            max-width: 100%;
            width: 571px;
        }
    }
}
