/* s-process-stage */
.s-process-stage {
    padding: 86px 0;
    @include xl {
        padding-top: 144px;
        padding-bottom: 163px;
    }
    .b-timline {
        max-width: 1035px;
        padding-bottom: 85px;
        margin-left: 16px;
        @include xl {
            margin-right: auto;
            margin-left: auto;
            padding-bottom: 146px;
        }
    }

}
.b-timline--process {
    .b-timline__item {
        @include xl {
            padding-left: 80px;
        }
        &:not(:last-child) {
            padding-bottom: 54px;
        }
        &::before {
            left: -2px;
        }
        &:first-child {
            &::before {
                top: 30px;
            }
        }
        &:last-child {
            &::before {
                bottom: auto;
                height: 46px;
                @include xl {
                    height: 76px;
                }
            }
        }
    }
    .b-timline__text {
        position: relative;
        top: -10px;
        padding-left: 22px;
        @include xl {
            top: 0;
        }
        &::before {
            top: 20px;
            @include xl {
                top: 38px;
                left: -96px;
            }
        }
    }
    .b-timline__item:first-child::after {
        @include max-xl {
            top: -5px;
        }
    }
    .b-list {
        margin-bottom: 29px;
    }
}
.s-process-stage__img-1 {
    position: absolute;
    top: 115px;
    @include max-xl {
        display: none;
    }
}
.b-stage {
    max-width: 970px;
    margin-top: -38px;
    padding: 40px 28px 22px;
    background-color: $color-gray;
    @include xl {
        padding: 60px 80px 36px;

    }
}
.b-stage__title {
    margin-bottom: 3px;
    @include max-xl {
        font-size: r(18);
    }
}
.b-stage__right {
    @include xl {
        margin-left: 83px;
    }
}
