/* s-home-work */
.s-home-work__inner {
    padding-top: 70px;
    max-width: 1060px;
    margin-right: auto;
    margin-left: auto;
    @include lg {
        padding: 131px 0 148px;
    }

}
.s-home-work__title {
    letter-spacing: -.02em;
    margin-bottom: 16px;
    @include xl {
        margin-left: -9px;
    }
}
.s-home-work__title-text {
    margin-bottom: 65px;
    @include xl {
        margin-left: -9px;
    }
}
.s-home-work__img {
     margin-bottom: -190px;
    @include xl {
        position: relative;
        top: 4px;
        left: -8px;
        margin-bottom: 0;
    }
}
.s-home-work {
    .b-timline {
        margin-left: 17px;
        margin-bottom: 50px;
        @include lg {
            margin-left: 60px;
            margin-bottom: 0;
        }
    }
    .b-timline__item {
        @include fc {
            &::before {
                top: 40px;
            }
        }
        @include lc {
            &::before {
                bottom: 54px;
            }
        }
    }
}
