/* s-hero--study */
.s-hero--study {
    padding-top: 105px;
    padding-bottom: 90px;
    @include xl {
        padding-top: 180px;
        padding-bottom: 0;
    }
    .s-hero__top {
        margin-bottom: 44px;
        @include xl {
            margin-bottom: 68px;
        }
    }
    .s-hero__title {
        text-align: left;
        @include max-xl {
            font-size: r(28);
        }
    }
    .s-hero-bottom {
        padding-bottom: 0;
    }
    .b-list {
        &::after {
            @include xl {
                content: "";
            }
            position: absolute;
            top: 51px;
            left: -173px;
            width: 130px;
            height: 39px;
            background-image: url("data:image/svg+xml,%3Csvg width='130' height='39' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M129.023 17.469a2.5 2.5 0 00-.554-3.492L110.261.757a2.499 2.499 0 10-2.937 4.047l16.184 11.75-11.75 16.185a2.5 2.5 0 004.046 2.937l13.219-18.207zm-127.631 21l126-20-.784-4.938-126 20 .784 4.938z' fill='%23000'/%3E%3C/svg%3E");
            background-color: transparent;
            background-repeat: no-repeat;
            background-size: cover;
        }
        margin-bottom: 50px;
        @include xl {
            margin-bottom: 20px;
        }
        li:not(:last-child) {
            margin-bottom: 15px;
        }
    }
    .s-hero-bottom__case {
        @include md {
            margin-left: 14px;
        }
        p {
            margin-bottom: 19px;
            @include xl {
                margin-bottom: 5px;
            }
        }
    }
    .s-hero__img {
        position: absolute;
        width: 163px;
        bottom: -57px;
        right: 22px;
        @include xl {
            position: relative;
            top: 10px;
            right: -50px;
            width: auto;
            margin-bottom: -52px;
        }
        @include xxl {
            right: -136px;
        }
    }
}
