/* b-form */
.b-form {
    .form-group {
        margin-bottom: 12px;
        @include xl {
            margin-bottom: 21px;
        }
    }
    .form-control {
        font-size: $d-paragraph-extra-small;
        line-height: $d-line-height-6;
        height: auto;
        padding: 13px 22px;
        border-color: transparent;
        background: $color-gray;
        border-radius: $b-radius-10;
        box-shadow: none !important;
        color: $color-root;
        -webkit-transition: all .15s linear;
        transition: all .15s linear;
        &:hover {
            border-color:transparent;
            background-color: $color-3;
            color: $color-blue;
        }
        &:focus,
        &:active {
            border-color: $color-root;
            background-color: $color-white;
            color: $color-root;
        }
    }
    label {
        font-size: $d-paragraph-extra-small;
        line-height: $d-line-height-6;
        margin-bottom: 0;
    }
    .b-form__pp {
        display: block;
        max-width: 313px;
        font-size: $d-tiny;
        line-height: $d-line-height-7;
        color: $color-05-root;
    }
}
