/* s-work-expert */
.s-work-expert {
    padding-top: 60px;
    padding-bottom: 86px;
    @include xl {
        margin-top: 137px;
        padding-top: 120px;
    }
    .p-section__title {
        margin-bottom: 20px;
        @include xl {
            margin-bottom: 67px;
        }
    }
    .b-list--mark-2 li {
        @include max-xl {
            font-size: r(18);
        }
        &:not(:last-child) {
            margin-bottom: 14px;
        }
    }
}
.b-list--expert {
    width: 100%;
    @include sm {
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    }
    li {
        padding-right: 10px;
        @include sm {
            -webkit-flex: 0 1 50%;
            -ms-flex: 0 1 50%;
            flex: 0 1 50%;
            width: 50%;
            max-width: 50%;
            min-width: 50%;
        }
        &:nth-child(even) {
            @include xl {
                position: relative;
                left: 11px;
            }
        }
    }
}
.s-work-expert__decor {
    position: absolute;
    top: 25px;
    right: 8px;
    @include xxl {
        right: -53px;
    }
    &::before,
    &::after {
        content: "";
        position: absolute;
        top: 0;
        background: $color-root;
    }
    &::before {
        right: 0;
        width: 90px;
        height: 3px;

    }
    &::after {
        bottom: 0;
        width: 3px;
        height: 325px;
    }
}
.s-work-expert__img {
    position: absolute;
    right: 0;
    z-index: 1;
    bottom: -166px;
    @include xl {
        right: -40px;
        bottom: -163px;
    }
    @include xxl {
        right: -101px;
    }
}
