/* s-post-main */
.s-post-main {
    padding-top: 105px;
    padding-bottom: 180px;
    @include md {
        padding-bottom: 100px;
    }
    @include xl {
        padding-top: 155px;
        padding-bottom: 356px;
    }
    font-size: $m-paragraph-normal;
    line-height: $m-line-height-6;
    @include xl {
        font-size: $d-paragraph-normal;
        line-height: 145%;
    }
    img {
        overflow: hidden;
        border-radius: $b-radius-10;
        margin-bottom: 20px;
        max-width: 100%;
        width: auto;
        height: auto;
        @include xl {
            margin-bottom: 55px;
        }
    }
    h1 {
        font-size: $m-heading-3;
        line-height: $m-line-height-3;
        letter-spacing: -.02em;
        @include xl {
            font-size: $d-heading-2;
            line-height: $d-line-height-2;
            margin-bottom: 64px;
        }
    }
    h2 {
        font-size: $m-heading-4;
        line-height: $m-line-height-4;
        letter-spacing: -.02em;
        @include xl {
            font-size: $d-heading-3;
            line-height: 120%;
            margin-bottom: 28px;
        }
    }
    p {
        margin-bottom: 40px;
        & + h2 {
            margin-top: 89px;
            @include xl {
                margin-top: 56px;
            }
        }
    }
    .container-narrow-xs {
        &::after,
        &::before {
            position: absolute;
            background-color: transparent;
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            @include xl {
                content: "";
            }
        }
        &::after {
            top: 85px;
            right: 30px;
            width: 52px;
            height: 63px;
             background-image: url('../img/post-main/1.svg');
        }
        &::before {
            top: 100px;
            right: -77px;
            width: 177px;
            height: 146px;
             background-image: url('../img/post-main/2.svg');
        }
    }
}
